<template>
  <div class="class-hour-wait">
    <head-top title="连接授课软件">
      <span slot="right" @click="toRefresh">
           <van-icon name="replay" class="head-right"></van-icon>
      </span>
    </head-top>

    <div class="sub_module">
      <div class="message_title">
        <div>1、请在电脑上打开睿课教授课端</div>
        <div>2、点击授权码登录并输入授权码</div>
      </div>
      <div class="flex-evenly message_content">
        <van-tag plain type="success" size="large" v-for="(item, index) in codeArr.length" disabled :key="index">
          {{codeArr[index]}}
        </van-tag>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: "wait",
  data () {
    return {
      codeValue: ""
    };
  },
  computed: {
    codeArr () {
      return this.codeValue.split('');
    }
  },
  created () {
    this.toRefresh()
  },
  methods: {
    toRefresh () {
      this.codeValue = "";
      // for (let i = 0; i < 4; i++) {
      //   this.codeValue += Math.floor(Math.random() * 16.0).toString(16);
      // }
      this.$mainHttp.classHour.freshLinkCode({
        chID: this.$store.getters.classHourID,
        teacherID: this.$store.getters.teacherID,
        classID: this.$store.getters.classID,
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.$toast("更新成功");
          this.codeValue = res["ScreenCode"];
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("更新失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";

.class-hour-wait {
  background-color: #fff;
  min-height: 100%;

  .message_title {
    margin-top: 50px;
    font-size: @font-title;
    text-align: center;
  }

  .message_content {
    margin-top: 50px;

    /deep/ .van-tag--large {
      font-size: @font-title * 1.65;
    }
  }
}
</style>
